




































































































































































































































































































































































































































































































import { Vue, Component } from 'vue-property-decorator';
import { BaseLayoutData } from '@/types/layouts/baseLayout.interface';
import { mapState } from 'vuex';
import BaseImg from '@/components/BaseImg.vue';
import BaseAvatar from '@/components/BaseAvatar.vue';

@Component({
  components: {
    BaseImg,
    BaseAvatar
  },

  data(): BaseLayoutData {
    return {
      // 选项卡导航
      navTabs: [
        {
          label: '首页',
          to: { name: 'home' }
        },
        // {
        //   label: '产品',
        //   to: { name: 'product' }
        // },
        {
          label: '设计',
          to: { name: 'design' }
        },
        {
          label: '开发',
          to: { name: 'develop' }
        },
        {
          label: '组件',
          to: { name: 'home_components' }
        },
        {
          label: '我的',
          to: { name: 'my' }
        }
      ],
      // 底部导航
      navBottomNavigations: [
        {
          label: '首页',
          icon: 'D3III16glMobius icon-16gl-U',
          to: { name: 'home' }
        },
        // {
        //   label: '产品',
        //   icon: 'D3III16glMobius icon-16gl-P',
        //   to: { name: 'product' }
        // },
        {
          label: '设计',
          icon: 'D3III16glMobius icon-16gl-X',
          to: { name: 'design' }
        },
        {
          label: '开发',
          icon: 'D3III16glMobius icon-16gl-D',
          to: { name: 'develop' }
        },
        {
          label: '组件',
          icon: 'D3III16glMobius icon-16gl-C',
          to: { name: 'home_components' }
        },
        {
          label: '我的',
          icon: 'D3III16glMobius icon-16gl-exclamation-m',
          // badge: {
          //   // content: '13',
          //   dot: true
          // },
          to: { name: 'my' }
        }
      ],
      // 登录相关信息
      loginInfo: null,
      // 页脚
      footer: {
        buttons: [
          {
            label: '关于我们',
            path: '/about'
          },
          {
            label: '用户协议',
            path: '/'
          },
          {
            label: '隐私政策',
            path: '/'
          },
          {
            label: '帮助中心',
            path: '/'
          },
          {
            label: '联系我们',
            path: '/'
          }
        ]
      },
      fab: false // 浮动按钮
    };
  },

  computed: {
    ...mapState([
      'title', // 当前页面标题
      'breadcrumbs', // 当前页面面包屑导航
      'overlay', // 页面遮罩层状态
      'loading', // 页面加载状态 / HTTP请求加载状态
      'skeletonLoader' // 页面骨架加载器
    ])
  }
})
export default class BaseLayout extends Vue {
  title!: string; // 当前页面标题
  breadcrumbs!: object[]; // 当前页面面包屑导航
  overlay!: boolean; // 页面遮罩层状态
  loading!: boolean; // 页面加载状态 / HTTP请求加载状态
  skeletonLoader!: boolean; // 页面骨架加载器

  navTabs!: object[]; // 选项卡导航
  navBottomNavigations!: object[]; // 底部导航
  loginInfo!: object | null; // 登录相关信息
  footer!: object; // 页脚
  fab!: boolean; // 浮动按钮

  // 页面遮罩 / 加载 / HTTP请求总状态
  get pageLoading() {
    if (!this.overlay && !this.loading) {
      // 遮罩/加载都为false时，加载才关闭
      return false;
    }
    return true;
  }
  // 页面标题相关
  get pageTitle() {
    return this.$route.meta.isDynamicTitle
      ? this.title
      : this.$route.meta.title;
  }
  // 页面面包屑导航相关
  get pageBreadcrumbs() {
    return this.$route.meta.isDynamicTitle
      ? this.breadcrumbs
      : this.$route.meta.breadcrumbs;
  }

  // 断点：是否为移动端尺寸
  get isMobile() {
    return this.$vuetify.breakpoint.xsOnly;
  }

  // 是否显示底部导航
  get isShowBottomNavigation() {
    const paths = ['home', 'design', 'develop', 'my'];
    return this.isMobile && paths.includes(String(this.$route.name));
  }

  // 路由跳转相关
  goToPage(to: any) {
    if (to.name) {
      // 如果跳转路由和当前路由一致，则不跳转
      if (this.$route.name === to.name) {
        return;
      }
      this.$router.push({
        name: to.name
      });
    } else if (to.path) {
      // 如果跳转路由和当前路由一致，则不跳转
      if (this.$route.path === to.path) {
        return;
      }
      this.$router.push({
        path: to.path
      });
    }
  }
  // 判断路由名称
  isRouteName(name: any) {
    if (typeof name === 'string') {
      return this.$route.name === name;
    } else {
      return name.includes(this.$route.name);
    }
  }

  // 退出登录
  loginOut() {
    // 1. 向后端发起退出登录请求

    // 2. 清除 vuex和 localStorage / sessionStorage 的上储存的 token 登录信息
    this.$store.commit({ type: 'LOGIN_OUT', isAdmin: false });

    // 3. 刷新当前页面
    this.$router.go(0);
  }

  // 添加文章
  addArticle() {
    if (this.loginInfo) {
      this.goToPage({ path: '/add-article' });
    } else {
      this.goToPage({ name: 'login' });
    }
  }

  // 获取登录信息后台数据
  fetchLoginInfoData() {
    // this.$api.userApi.baseLayout.getLoginInfo().then(
    //   (response: any) => {
    //     // console.log(response.data);
    //     this.loginInfo = response.data;
    //   },
    //   (error: any) => {
    //     console.log(`${error.response.status}: ${error.response.statusText}`);
    //   }
    // );

    this.loginInfo = this.$api.userApi.baseLayout.getLoginInfo().data;
  }
  // 消息总计数
  get messageCount() {
    return (item: any) => {
      const items = item.items;
      let count = 0;
      items.forEach((element: any) => {
        if (
          element.action &&
          element.action.badge &&
          element.action.badge.content
        ) {
          count += Number(element.action.badge.content);
        }
      });
      return count > 99 ? '99+' : count;
    }
  }

  // 生命周期钩子函数
  created() {
    // this.fetchLoginInfoData();
  }
}
